import React, { useEffect, useRef, useState, useMemo } from 'react';
import { HeatmapLayer } from '@deck.gl/aggregation-layers/typed';
import { Wrapper, Status } from '@googlemaps/react-wrapper';
import { GoogleMapsOverlay as DeckOverlay } from '@deck.gl/google-maps/typed';
import citizenSecurityHeatMapData from './data/citizenSecurityHeatMap.json';

const API_KEY = 'AIzaSyC2x07DZufLXwgwUfLwMli6OH32kh1PYwE';
const MAP_ID = "f66401fe0f10dc97";
const DATA = citizenSecurityHeatMapData;

const renderMap = (status) =>
{
    if (status === Status.LOADING) return <h3>{status} ..</h3>;
    if (status === Status.FAILURE) return <h3>{status} ...</h3>;
    return null;
};

function MapEditor ({ center, zoom, data })
{
    const ref = useRef();
    const [map, setMap] = useState();

    const overlay = useMemo(
        () =>
            new DeckOverlay({
                layers: new HeatmapLayer({
                    data: DATA,
                    getPosition: d => [d.longitude, d.latitude],
                    getWeight: d => d.n_killed + (d.n_injured * 0.5),
                    radiusPixels: 60
                })
            }),
        []
    );

    useEffect(() =>
    {
        if (map)
        {
            map.setCenter(center);
            map.setZoom(zoom);
            overlay.setMap(map);
        }
    }, [map, center, zoom, overlay]);

    useEffect(() =>
    {
        const mapInstance = new window.google.maps.Map(ref.current, { mapId: MAP_ID });
        setMap(mapInstance);
    }, []);
    return (
        <>
            <div ref={ref} id="map" style={{ height: '100vh', width: '100wh' }} />
        </>
    );
}

export default function Root ({ center, zoom })
{
    return (
        <>
            <Wrapper apiKey={API_KEY} render={renderMap}>
                <MapEditor center={center} zoom={zoom} data={{}} />
            </Wrapper>
        </>
    );
}