import React, { useEffect, useRef, useState, useMemo } from 'react';
import { GeoJsonLayer } from 'deck.gl/typed';
import { Wrapper, Status } from '@googlemaps/react-wrapper';
import { GoogleMapsOverlay as DeckOverlay } from '@deck.gl/google-maps/typed';
import {GOOGLE_MAPS_API_KEY} from '../MapSettings';
import {GOOGLE_MAPS_MAP_ID} from '../MapSettings';
import featureImproveMapData from './data/featureImproveMapData.json';

const DATA = featureImproveMapData;

const renderMap = (status) =>
{
    if (status === Status.LOADING) return <h3>{status} ..</h3>;
    if (status === Status.FAILURE) return <h3>{status} ...</h3>;
    return null;
};

function MapEditor ({ center, zoom, data })
{
    const ref = useRef();
    const [map, setMap] = useState(null);

    const overlay = useMemo(
        () =>
            new DeckOverlay({
                layers: DATA.map((layer, i) =>
                    new GeoJsonLayer({
                        id: 'districts' + i,
                        data: layer.url,
                        // Styles
                        filled: true,
                        stroked: false,
                        pointRadiusMinPixels: 2,
                        pointRadiusScale: 2000,
                        getPointRadius: (f) => (11 - f.properties.scalerank),
                        getFillColor: [10, 10, 10, 100],
                        // Interactive props
                        pickable: true,
                        autoHighlight: true,
                        onClick: info =>
                            info.object &&
                            console.log(info.object.properties)
                    }),
            )
            })
        ,
        []
    );

    useEffect(() =>
    {
        if (map)
        {
            map.setCenter(center);
            map.setZoom(zoom);
            overlay.setMap(map);
        }
    }, [map, center, zoom, overlay]);

    useEffect(() =>
    {
        const mapInstance = new window.google.maps.Map(ref.current, { mapId: GOOGLE_MAPS_MAP_ID });
        setMap(mapInstance);
    }, []);
    return (
        <>
            <div ref={ref} id="map" style={{ height: '100vh', width: '100wh' }} />
        </>
    );
}

export default function Root ({ center, zoom })
{
    return (
        <>
            <Wrapper apiKey={GOOGLE_MAPS_API_KEY} render={renderMap}>
                <MapEditor center={center} zoom={zoom} data={{}} />
            </Wrapper>
        </>
    );
}