import 'animate.css';
import './content/base.css';
import './content/common.css';
import Home from './components/screens/Home';
import { Routes, Route, BrowserRouter } from 'react-router-dom';
import SignIn from './components/screens/Authentication/SignIn';
import MapWithKML from './components/common/Maps/FeatureImproveMap/FeatureImproveMap';
import SignUp from './components/screens/Authentication/SignUp';
import Ad from './components/screens/Advertisement/View';
import CitizenSecurityHeatMap from './components/common/Maps/CitizenSecurityHeatMap';
import AdvertisementCreate from './components/screens/Advertisement/Create';
import Profile from './components/screens/Account/Profile';
import NearbyPlace from './components/common/Advertisement/subcomponents/NearbyPlace';

function App ()
{
    const mapConfig = {
        center: { lat: -12.022848, lng: -77.0706962 },
        zoom: 12,
    }
    const mapConfigHM = {
        center: { lat: 40, lng: -100.0 },
        zoom: 10,
    }

    return (
        <>
            <BrowserRouter>
                <Routes>
                    {/* Advertisement */}
                    <Route path="/" element={<Home />} />
                    <Route path="/advertisement/view/:advertisementId" element={<Ad />} />
                    <Route path="/advertisement/create" element={<AdvertisementCreate />} />

                    {/* Authentication */}
                    <Route path="/signin" element={<SignIn showCloseBtn={false} />} />
                    <Route path="/signup" element={<SignUp />} />

                    {/* Maps */}
                    <Route path='geojson' element={<MapWithKML
                        center={mapConfig.center}
                        zoom={mapConfig.zoom}
                    />} />
                    <Route path='heatmap' element={<CitizenSecurityHeatMap
                        center={mapConfigHM.center}
                        zoom={mapConfigHM.zoom}
                    />} />

                    {/* Account */}
                    <Route path='account/profile' element={<Profile />} />


                    {/* Account */}
                    <Route path='nearby' element={<NearbyPlace />} />
                </Routes>
            </BrowserRouter>
        </>
    );
}

export default App;
