import React, { Component } from 'react';
import type { Props, State } from './ListView.types';
import Advertisement from '../../../../common/Advertisement';

class ListView extends Component<Props, State>
{
    static defaultProps = {
        advertisements: [],
    }
    render ()
    {
        return (
            <>
                <div className='p-4'></div>
                <div className='row row-cols-1 row-cols-sm-1 row-cols-md-2 row-cols-xl-3 align-items-stretch g-3 mt-0 py-3 px-2 mx-0 animate__animated animate__slideInUp animate__fast'>
                    {
                        this.props.advertisements.map((ad: any, i: number) =>
                        {
                            return (<div key={'ad' + i} className='col'>
                                <Advertisement content={ad} />
                            </div>)
                        })
                    }
                </div>
            </>
        );
    }
}

export default ListView;